
$c1: #00A3FF
$c1-lt: #a8dfff
$c2: #FF5700
$c2-lt: #B5A3EF
$c3: #C1EF5A 

$c-dkst: #222
$c-dk: #1C202E
$c-lt: #aaa
$c-gr-lt: #555
$c-gr-dk: #888

$c-outline: #40E0D0

$gradient: linear-gradient(164deg, #414B6F 0%, #1C202E 48.08%, #070910 98.88%)

$ex-view-color: $c2-lt

$c-t-success: #59B100
$c-t-failed: #C91919
// $c-t-loading: #C79002
$c-t-loading: $c1
$c-t-dark: #292a38
$c-t-light: #3c3d52

$colors: ("purple": #7E53C1, "orange": #FF944D, "dark-blue": #003366, "blue": #00A3FF, "red": #FF5700, "yellow": #FFD700, "green": #4CAF50, "pink": #FF4081)


@mixin color-fill($class-name, $color)
  &.#{$class-name}
    background-color: $color

@mixin color-text($class-name, $color)
  &.#{$class-name}
    color: $color

@mixin color-accordian($class-name, $color)
  &.#{$class-name}
    border: 1px solid $color
    h2
      color: $color

@mixin color-stroke($class-name, $color)
  &.#{$class-name}
    stroke: $color


$sp_0: .6rem
$sp_1: .8rem
$sp_2: 1rem
$sp_3: 1.2rem
$sp_4: 1.6rem
$sp_5: 2rem
$sp_6: 2.4rem
$sp_7: 3.2rem
$sp_8: 4rem
$sp_9: 4.8rem
$sp_10: 6rem
$sp_11: 7.2rem
$sp_12: 8.4rem
$sp_13: 10rem
$sp_14: 12rem

$fs_0: 1.2rem
$fs_1: 1.4rem
$fs_2: 1.6rem
$fs_2-b: 1.8rem
$fs_3: 2rem
$fs_4: 2.4rem
$fs_5: 3rem
$fs_6: 3.6rem
$fs_7: 4.8rem
$fs_8: 6.4rem
$fs_9: 8.6rem

$br_sm: 5px
$br_md: 10px
$br_lg: 20px

@mixin text-xsm
  font-size: $fs_2
  font-weight: 400
  color: $c-lt

@mixin text-sm
  font-size: $fs_2-b
  font-weight: 400
  color: $c-lt

@mixin text-dt($bold: false)
  font-size: $fs_3
  font-weight: 400
  color: $c-lt
  @if $bold
    font-weight: 700

@mixin text-md($bold: false)
  font-size: $fs_4
  font-weight: 500
  @if $bold
    font-weight: 700
  
@mixin text-h1
  font-size: $fs_6
  font-weight: 700
  color: $c-dk

@mixin text-calender-header
  font-size: $fs_4
  font-weight: 700
  color: $c-lt

@mixin text-button
  font-size: $fs_2
  font-weight: 700
  letter-spacing: .6px
  text-transform: uppercase
  color: #fff

@mixin text-placeholder
  font-size: $fs_2-b
  color: $c-gr-lt

@mixin settings-header
  font-size: $fs_4
  color: $c-lt
  font-weight: 600

@mixin settings-item
  font-size: $fs_4
  color: $c-lt
  font-weight: 400

@mixin section-styles
  width: 100%
  padding: $sp_6
  border-radius: $br_md

.w-full
  width: 100%

  // GLOBAL STYLES

*
  box-sizing: border-box
  margin: 0
  padding: 0

html
  font-family: 'Inter'
  font-size: 62.5%

body
  background-color: $c-dk
  padding: 0 $sp_6
  max-width: 40rem
  margin: 0 auto

main
  margin-top: $sp_6

a
  text-decoration: none

ul
  list-style: none

button
  cursor: pointer

.center
  display: flex
  align-items: center
  justify-content: center

.stroke-primary
  stroke: $c1


