.w-full {
  width: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Inter";
  font-size: 62.5%;
}

body {
  background-color: #1C202E;
  padding: 0 2.4rem;
  max-width: 40rem;
  margin: 0 auto;
}

main {
  margin-top: 2.4rem;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.stroke-primary {
  stroke: #00A3FF;
}